/* –ó–∞–¥–∞–Ω–∏–µ –Ω–∞ —É—Ä–æ–∫:

1) –ü–µ—Ä–≤—É—é —á–∞—Å—Ç—å –∑–∞–¥–∞–Ω–∏—è –ø–æ–≤—Ç–æ—Ä–∏—Ç—å –ø–æ —É—Ä–æ–∫—É

2) –°–æ–∑–¥–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é showMyDB, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –ø—Ä–æ–≤–µ—Ä—è—Ç—å —Å–≤–æ–π—Å—Ç–≤–æ privat. 
–ï—Å–ª–∏ —Å—Ç–æ–∏—Ç –≤ –ø–æ–∑–∏—Ü–∏–∏ false - –≤—ã–≤–æ–¥–∏—Ç –≤ –∫–æ–Ω—Å–æ–ª—å –≥–ª–∞–≤–Ω—ã–π –æ–±—ä–µ–∫—Ç –ø—Ä–æ–≥—Ä–∞–º–º—ã

3) –°–æ–∑–¥–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é writeYourGenres –≤ –∫–æ—Ç–æ—Ä–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –±—É–¥–µ—Ç 3 —Ä–∞–∑–∞ –æ—Ç–≤–µ—á–∞—Ç—å 
–Ω–∞ –≤–æ–ø—Ä–æ—Å "–í–∞—à –ª—é–±–∏–º—ã–π –∂–∞–Ω—Ä –ø–æ–¥ –Ω–æ–º–µ—Ä–æ–º ${–Ω–æ–º–µ—Ä –ø–æ –ø–æ—Ä—è–¥–∫—É}". 
–ö–∞–∂–¥—ã–π –æ—Ç–≤–µ—Ç –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è –≤ –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö genres

P.S. –§—É–Ω–∫—Ü–∏–∏ –≤—ã–∑—ã–≤–∞—Ç—å –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ*/

const personalMovieDB = {
    count: {},
    movies: {},
    actors: {},
    genres: [],
    privat: false
};

function start() {
    personalMovieDB.count = prompt('–°–∫–æ–ª—å–∫–æ —Ñ–∏–ª—å–º–æ–≤ –≤—ã —É–∂–µ –ø–æ—Å–º–æ—Ç—Ä–µ–ª–∏?', "");
    while (personalMovieDB.count == '' || personalMovieDB.count == null || isNaN(personalMovieDB.count)) {
        alert('Error. –û—Ç–≤–µ—Ç –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —á–∏—Å–ª–æ–º, –∏ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º');
        personalMovieDB.count = prompt('–°–∫–æ–ª—å–∫–æ —Ñ–∏–ª—å–º–æ–≤ –≤—ã —É–∂–µ –ø–æ—Å–º–æ—Ç—Ä–µ–ª–∏?', "");
    }
}

function rememberMyFilms() {
    for (let i = 1; i <= personalMovieDB.count; i++) {
        let a = null,
            b = null;
        a = prompt('–û–¥–∏–Ω –∏–∑ –ø–æ—Å–ª–µ–¥–Ω–∏—Ö –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã—Ö —Ñ–∏–ª—å–º–æ–≤?', '');
        b = prompt('–ù–∞ —Å–∫–æ–ª—å–∫–æ –æ—Ü–µ–Ω–∏—Ç–µ –µ–≥–æ?', '');

        if (a != null && b != null && a != '' && b != '' && a.length < 50) {
            personalMovieDB.movies[a] = b;
            console.log('Done');
        } else {
            console.log('error');
            i--;
        }
    }
}

function detectPersonalLevel() {
    if (personalMovieDB.count < 10) {
        console.log("–ü—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–æ –¥–æ–≤–æ–ª—å–Ω–æ –º–∞–ª–æ —Ñ–∏–ª—å–º–æ–≤");
    } else if (personalMovieDB.count >= 10 && personalMovieDB.count < 30) {
        console.log("–í—ã –∫–ª–∞—Å—Å–∏—á–µ—Å–∫–∏–π –∑—Ä–∏—Ç–µ–ª—å");
    } else if (personalMovieDB.count >= 30) {
        console.log("–í—ã –∫–∏–Ω–æ–º–∞–Ω");
    } else {
        console.log("–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞");
    }
}

function writeYourGenres() {
    let genreNum = 1;
    for (let i = 1; i <= 3; i++) {
        personalMovieDB.genres[i - 1] = (i + ". " + prompt(`–í–∞—à –ª—é–±–∏–º—ã–π –∂–∞–Ω—Ä –ø–æ–¥ –Ω–æ–º–µ—Ä–æ–º ${i}?`));
    }
}

function showMyDB() {
    let q = '–ü–æ–∫–∞–∑–∞—Ç—å –≤—Å–µ–º –≤–∞—à—É –∫–∏–Ω–æ–±–∞–∑—É? –î–∞ –∏–ª–∏ –ù–µ—Ç?'
    let a = prompt(q, '');
    while (a != '–î–∞' || a != '–¥–∞' || a != '–ù–µ—Ç' || a != '–Ω–µ—Ç' ) {
        alert('–ù–µ –≤–µ—Ä–Ω—ã–π –æ—Ç–≤–µ—Ç! –ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –≤–≤–æ–¥!');
        a = prompt(q, '');
    } if ( a == '–î–∞' || a == '–¥–∞') {
        personalMovieDB.privat = false;
    } else {
        personalMovieDB.privat = true;
    }
}

